.history {
  @media (max-width: $mobiles-max) {
    margin-top: 50px;
  }

  @media (max-width: 359px) {
    margin-top: 40px;
  }

  h2 {
    transform: rotate(-90deg);
    color: $color-default-white;
    margin: 50px 0 0 0;

    @media (min-width: $tablets) {
      margin: 35px 0 0 5px;
      font-size: 35px;
      font-style: normal;
      font-weight: 400;
      line-height: 35px;
      letter-spacing: 0em;
      text-align: right;

      &:after {
        position: absolute;
        content: "";
        height: 100%;
        width: 1px;
        background-color: #ffffff;
        bottom: 0;
        left: -10px;
      }
    }

    @media (min-width: $desktops) {
      margin: 40px 0 0 -20px;
      font-size: 35px;
      font-style: normal;
      font-weight: 400;
      line-height: 35px;
      letter-spacing: 0em;
      text-align: right;

      &:after {
        position: absolute;
        content: "";
        height: 100%;
        width: 1px;
        background-color: #ffffff;
        bottom: 0;
        left: -14px;
      }
    }

    @media (min-width: $desktops-4) {
      margin: 40px 0 0 0;
      font-size: 40px;
      font-weight: 400;
      line-height: 40px;
    }

    writing-mode: vertical-rl;
    transform: rotate(-180deg);
    color: #ffffff;

    @media (max-width: $mobiles-max) {
      display: none;
    }
  }
}

.history__wrapper {
  position: relative;
  display: flex;

  @media (min-width: $tablets) {
    &:before {
      position: absolute;
      content: "";
      bottom: 220px;
      left: 1%;
      width: 145px;
      height: 145px;
      background-image: url("../img/star_gold.png");
      background-size: contain;
    }
  }

  @media (min-width: $desktops) {
    &:before {
      position: absolute;
      content: "";
      bottom: -70px;
      left: 30%;
      width: 145px;
      height: 145px;
      background-image: url("../img/star_gold.png");
      background-size: contain;
    }
  }

  @media (min-width: $desktops-2) {
    &:before {
      left: 25%;
      bottom: -80px;
    }
  }

  @media (min-width: $desktops-4) {
    &:before {
      left: 28%;
      bottom: -115px;
      width: 200px;
      height: 200px;
    }
  }

  @media (max-width: $mobiles-max) {
    &:after {
      display: none;
    }
  }
}

.history__inner {
  display: flex;
  margin-top: 50px;

  @media (min-width: $tablets) {
    margin-top: 35px;
    flex-direction: column;
    padding-left: 5%;
  }

  @media (min-width: $desktops) {
    margin-top: 35px;
    flex-direction: row;
    padding-left: 0;
  }

  @media (min-width: $desktops-2) {
    margin-top: 23px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 50px;
  }

  @media (max-width: $mobiles-max) {
    display: flex;
    flex-direction: column;
    padding-top: 0;
    margin-top: 0;
  }

  p {
    text-indent: 30px;
    color: $color-default-white;
    // font-size: 20px;
    // font-style: normal;
    // font-weight: 400;
    // line-height: 27px;
    // letter-spacing: 0px;
    // text-align: left;
    // margin: 0 0 6% 0;

    @media (min-width: $tablets) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 19px;
      letter-spacing: 0px;
      text-align: left;
    }

    @media (min-width: $desktops) {
      font-size: 14px;
      line-height: 20px;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 27px;
    }

    @media (max-width: $mobiles-max) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      margin: 0;
    }
  }
}

.history__info {
  @media (min-width: $tablets) {
    // min-width: 330px;
    display: flex;
  }
  @media (min-width: $desktops) {
    flex-direction: column;
    // min-width: 330px;
    min-width: 32vw;
    margin-left: 4%;
    margin-right: 7%;
    width: 31%;
  }

  @media (min-width: $desktops-2) {
    // min-width: 375px;
    min-width: 28vw;

    margin-left: 3%;
    margin-right: 5%;
    width: 31%;
  }

  @media (min-width: $desktops-4) {
    // min-width: 528px;
    min-width: 28vw;
    margin-left: 3%;
    margin-right: 6%;
    width: 31%;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    margin: 0;
  }
}

.history__details {
  width: auto;
  flex-grow: 1;

  @media (min-width: $tablets) {
    padding-left: 20%;
    margin-top: 50px;
  }

  @media (min-width: $desktops) {
    display: flex;
    flex-direction: column;
    width: 61%;
    padding-left: 0;
    margin-top: 0;
  }

  @media (min-width: $desktops-2) {
    width: 61%;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
  }
}

.history__plan {
  height: auto;
  width: 405px;
  margin-top: 60px;

  @media (min-width: $tablets) {
    height: 210px;
    width: 267px;

    margin-top: 0;
  }

  @media (min-width: $desktops) {
    margin-top: 45px;
    width: 240px;
  }

  @media (min-width: $desktops-2) {
    margin-top: 45px;
    width: 270px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 65px;
    width: 405px;
  }

  @media (max-width: $mobiles-max) {
    display: none;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.history__video {
  width: 100%;
  position: relative;

  @media (min-width: $tablets) {
    height: 259px;
    width: 461px;
  }

  @media (min-width: $desktops) {
    // width: 435px;
    height: auto;
    width: 89%;
  }

  @media (min-width: $desktops-2) {
    // width: 545px;
    // width: 84%;
    width: 76%;
  }

  @media (min-width: $desktops-4) {
    // width: 864px;
    width: 86%;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  @media (max-width: $mobiles-max) {
    // height: 201px;
    position: relative;
    height: auto;
    margin-top: 20px;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      min-height: 201px;
    }
  }
}

.history__texts {
  @media (min-width: $tablets) {
    width: 52%;
    margin-right: 2%;
  }

  @media (min-width: $desktops) {
    width: auto;
    margin-right: 0;
  }
  p {
    @media (min-width: $tablets) {
      color: $color-default-white;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 19px;
      letter-spacing: 0px;
      text-align: left;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: 27px;
      letter-spacing: 0px;
      text-align: left;
    }
    text-indent: 30px;

    @media (max-width: $mobiles-max) {
      text-indent: 0;
    }
  }
}

.history__house-text {
  max-width: 910px;

  h3 {
    margin: 20px 0 30px;
    position: relative;

    @media (min-width: $tablets) {
      &:before {
        content: url("../img/heading.svg");
        position: absolute;
        left: -30px;
        top: -14px;
        z-index: -1;
      }
    }

    @media (min-width: $desktops) {
      &:before {
        content: url("../img/heading.svg");
        position: absolute;
        left: -30px;
        top: -14px;
        z-index: -1;
      }
    }

    @media (min-width: $desktops) {
      font-size: 25px;
      line-height: 25px;
    }

    @media (min-width: $desktops-4) {
      font-size: 30px;
      line-height: 30px;
    }

    @media (max-width: $mobiles-max) {
      margin: 15px 0 25px;
    }
  }

  @media (min-width: $tablets) {
    padding-right: 4%;
  }

  @media (min-width: $desktops) {
    padding-right: 10%;
  }

  @media (min-width: $desktops-2) {
    width: 100%;
    padding-right: 14%;
  }

  @media (min-width: $desktops-4) {
    width: 100%;
    padding-right: 0;
  }

  p {
    text-indent: 30px;

    &:first-of-type {
      margin-bottom: 0;
    }

    @media (max-width: $mobiles-max) {
      text-indent: 0;

      &:first-of-type {
        margin-bottom: 70px;
      }
    }
  }
}
