.introduction__wrapper {
  position: relative;

  h1 {
    @media (min-width: $desktops) {
      margin: 30px 0 0 0;
    }

    @media (min-width: $desktops-2) {
      margin: 15px 0 0 0;
    }

    @media (min-width: $desktops-4) {
      margin: 60px 0 0 0;
    }

    @media (max-width: $desktops-1) {
      width: 560px;
    }

    @media (max-width: $mobiles-max) {
      margin: 41px 0 0 0;
      z-index: 2;
      position: relative;
      width: 327px;
    }

    @media (max-width: 360px) {
      margin: 41px 0 0 0;
      z-index: 2;
      position: relative;
      width: 269px;
    }
  }
}

.introduction__inner {
  display: flex;
  position: relative;
}

.introduction__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.introduction__title {
  width: 846px;
  z-index: 2;

  @media (max-width: $mobiles-max) {
    width: auto;
  }
}

.introduction__star {
  position: absolute;

  @media (min-width: $desktops) {
    top: -5%;
    left: 57%;
    width: 125px;
    height: 125px;
  }

  @media (min-width: $desktops-2) {
    left: 53%;
    width: 180px;
    height: 180px;
  }

  @media (min-width: $desktops-4) {
    top: -90px;
    left: 50%;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__star-rhomb {
  position: absolute;

  @media (min-width: $desktops) {
    width: 100px;
    height: 100px;
    top: 38%;
    right: -10px;
  }

  @media (min-width: $desktops-2) {
    width: 140px;
    height: 140px;
    top: 32%;
  }

  @media (min-width: $desktops-4) {
    right: 20px;
    top: 26%;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__scheme {
  height: 304px;
  width: 486px;

  @media (max-width: $mobiles-max) {
    // height: 145px;
    // width: 230px;
    height: auto;
    width: 83%;
    max-width: 345px;
    max-height: 280px;
    margin-right: auto;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__note {
  position: relative;
  background-color: $color-blue;
  border-radius: 50%;
  width: 288px;
  height: 288px;
  margin-bottom: 0;
  margin-top: 30px;

  @media (min-width: $tablets) {
    transform: translate(305px, -335px);
  }

  @media (min-width: $desktops) {
    margin-top: 70px;
    transform: translate(0, 0);
  }

  @media (min-width: $desktops-2) {
    margin-top: 40px;
  }

  @media (max-width: $mobiles-max) {
    width: 160px;
    height: 160px;
    position: relative;
    /* position: absolute; */
    /* bottom: 6px; */
    margin-right: auto;
    margin-top: 62px;
    margin-bottom: 0;
  }

  @media (max-width: 359px) {
    margin-top: 70px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 110px;
  }

  @media (max-width: $desktops-3) {
    width: 215px;
    height: 215px;
    margin-bottom: 0;
  }

  @media (max-width: $mobiles-max) {
    width: 160px;
    height: 160px;
    position: relative;
    /* position: absolute; */
    /* bottom: 6px; */
    margin-right: auto;
    margin-top: 70px;
  }

  p {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    font-size: 16px;
    line-height: 18px;
    color: $color-default-white;
    text-align: center;

    position: absolute;
    padding: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotate(-8deg);
    width: 80%;

    @media (max-width: $mobiles-max) {
      font-size: 12px;
      font-style: normal;
      transform: rotate(0);
      padding: 60px 5px;
      text-align: center;
      line-height: 14px;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 20px;
    }

    @media (max-width: $desktops-3) {
      font-size: 16px;
      line-height: 18px;
    }

    @media (max-width: $mobiles-max) {
      font-size: 12px;
      font-style: normal;
      transform: translate(-50%, -50%) rotate(0);
      text-align: center;
      line-height: 14px;
    }
  }
}

.introduction__pictures {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  margin-top: 0;

  @media (max-width: $mobiles-max) {
    margin-top: -15px;
  }
}

.introduction__painting {
  height: auto;
  position: relative;

  @media (min-width: $tablets) {
    width: 76vw;
    margin: 0;
    position: absolute;
    right: -5%;
    bottom: -250px;
    max-width: 700px;
    min-width: 550px;

    &:after {
      position: absolute;
      content: "";
      width: 144px;
      height: 144px;

      background-image: url("../img/star_1366.png");
      top: 120px;
      left: -28%;
      background-repeat: no-repeat;
    }

    &:before {
      position: absolute;
      content: "";
      width: 114px;
      height: 114px;

      background-image: url("../img/star-1366-silver.png");
      top: -75px;
      left: 74%;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $desktops) {
    width: 47vw;
    margin: 0;
    position: absolute;
    right: 0;
    // bottom: -60px;
    bottom: -80px;
    max-width: none;
    min-width: none;

    &:after {
      position: absolute;
      content: "";
      width: 126px;
      height: 126px;

      background-image: url("../img/star_1024.png");
      top: -133px;
      left: 27%;
      background-repeat: no-repeat;
    }

    &:before {
      position: absolute;
      content: "";
      width: 100px;
      height: 100px;

      background-image: url("../img/star-1024-silver.png");
      top: 100px;
      left: 79%;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $desktops-2) {
    width: 43vw;
    bottom: -65px;
    right: 10px;

    &:after {
      position: absolute;
      content: "";
      width: 144px;
      height: 144px;

      background-image: url("../img/star_1366.png");
      top: -72px;
      left: 9%;
      background-repeat: no-repeat;
    }

    &:before {
      position: absolute;
      content: "";
      width: 114px;
      height: 114px;

      background-image: url("../img/star-1366-silver.png");
      top: 125px;
      left: 83%;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $desktops-4) {
    width: 44vw;
    bottom: -60px;
    right: 0;

    &:after {
      position: absolute;
      content: "";
      width: 180px;
      height: 180px;

      background-image: url("../img/star.png");
      top: -155px;
      left: 4%;
      background-repeat: no-repeat;
    }

    &:before {
      position: absolute;
      content: "";
      width: 140px;
      height: 140px;

      background-image: url("../img/star_rhomb.png");
      top: 160px;
      left: 80%;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: 2300px) {
    bottom: -55%;
    right: 0;
  }

  @media (max-width: $mobiles-max) {
    height: 246px;
    width: 100%;
    position: absolute;
    bottom: 50px;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}
