.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: none !important;
  border: none;
  transition: $transition-settings;
  cursor: pointer;
  flex-shrink: 0;

  &--toggle {
    background-color: transparent;
    width: 27px;
    position: absolute;
    width: 60px;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: $transition-settings;
    right: 34px;
    top: 47px;
    padding: 0;
    z-index: 2;

    @media (min-width: $desktops) {
      right: 57px;
    }

    @media (max-width: $mobiles-max) {
      width: 50px;

      right: 24px;
      top: 30px;
    }

    &:before {
      content: "";
      position: absolute;
      height: 3px;
      width: 50px;

      background-color: $color-white;
      transform: rotate(45deg);
      transition: $transition-settings;
    }

    &:after {
      content: "";
      position: absolute;
      height: 3px;
      width: 50px;

      background-color: $color-white;
      transform: rotate(-45deg);
      transition: $transition-settings;
    }

    &.close {
      &:before {
        height: 3px;
        width: 60px;
        top: 0;
        right: -4px;
        transform: rotate(180deg);
      }

      span {
        background-color: #ffffff;
        height: 3px;
        width: 60px;
        margin-top: -7px;
        transform: translateX(4px);
      }

      &:after {
        height: 3px;
        width: 60px;
        top: 17px;
        right: -4px;
        transform: rotate(180deg);
      }

      @media (max-width: $mobiles-max) {
        &:after {
          top: 8px;
          width: 50px;
        }

        &:before {
          width: 50px;
        }

        span {
          width: 50px;
        }
      }
    }

    @media (max-width: $tablets-max) {
      display: flex;
    }
  }

  &--round {
    border-radius: 50%;
    background-color: $color-blue;
    color: $color-default-white;

    @media (max-width: $mobiles-max) {
      font-size: 12px;
      font-style: normal;
      font-weight: 500;
      line-height: 13px;
      letter-spacing: 0px;
      text-align: center;
    }
  }

  &--epoque {
    position: absolute;
    top: -75px;
    right: 0;
    padding: 30px 0;
    display: flex;
    flex-direction: column;

    @media (max-width: $mobiles-max) {
      width: 110px;
      height: 110px;
      justify-content: space-between;
      padding: 30px 27px;
      top: -112px;
      left: 0;
      z-index: 1;
    }

    svg {
      width: 18px;
      height: 18px;
      margin-bottom: 5px;
    }
  }

  &--person {
    @media (min-width: $tablets) {
      width: 140px;
      height: 140px;
      top: -48px;
      right: -65%;
    }

    span {
      text-align: center;
      text-indent: 0;
    }

    @media (min-width: $desktops) {
      width: 140px;
      height: 140px;
      top: -55px;
      right: -70%;
    }

    @media (min-width: $desktops-2) {
      width: 140px;
      height: 140px;

      top: -50px;
      right: -170px;
      left: auto;
    }
    @media (min-width: $desktops-4) {
      width: 190px;
      height: 190px;

      top: -65px;
      right: -225px;
      left: auto;
    }
  }

  &--neighbour {
    position: relative;
    top: 0;
    left: 0;
  }

  &--play {
    background: transparent;
    top: 36px;
    position: absolute;

    margin: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &:before {
      background-image: url("../img/doodle.png");
      position: absolute;
      content: "";
      width: 113px;
      height: 106px;
      top: -35px;
      left: -10px;
    }
  }

  &--next {
    @media (min-width: $tablets) {
      content: "";
      display: flex;
      width: 170px;
      height: 170px;
      position: absolute;
      top: 365px;
      right: 8%;
      z-index: 1;
      svg {
        width: 20px;
        height: 20px;
        margin-bottom: 5px;
        margin-left: 75px;
      }
    }
    @media (min-width: $desktops) {
      content: "";
      display: flex;
      width: 170px;
      height: 170px;
      position: absolute;
      top: 140px;
      right: -5%;
      z-index: 1;
      svg {
        width: 20px;
        height: 20px;
        margin-bottom: 5px;
        margin-left: 75px;
      }
    }

    @media (min-width: $desktops-2) {
      content: "";
      display: flex;
      width: 170px;
      height: 170px;
      position: absolute;

      top: 250px;
      right: 10%;
      z-index: 1;

      svg {
        width: 20px;
        height: 20px;
        margin-bottom: 5px;
        margin-left: 80px;
      }
    }

    @media (min-width: $desktops-4) {
      content: "";
      display: flex;
      width: 220px;
      height: 220px;
      position: absolute;
      top: 37%;
      right: 11%;
      z-index: 1;

      svg {
        width: 30px;
        height: 30px;
        margin-bottom: 5px;
        margin-left: 110px;
      }
    }
  }

  &--play-big {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (min-width: $tablets) {
      width: 63px;
      height: 72px;
      background-image: url("../img/play_1024.svg");
      background-repeat: no-repeat;
    }

    @media (min-width: $desktops) {
      width: 63px;
      height: 72px;
      background-image: url("../img/play_1024.svg");
      background-repeat: no-repeat;
    }

    @media (min-width: $desktops-2) {
      width: 79px;
      height: 90px;
      background-image: url("../img/play_1366.svg");
      background-repeat: no-repeat;
    }

    @media (min-width: $desktops-4) {
      width: 126px;
      height: 144px;
      background-image: url("../img/play_big.svg");
      background-repeat: no-repeat;
    }
    &:before {
      display: none;
    }
  }

  &--closer {
    @media (min-width: $tablets) {
      top: -44px;
      left: 0;
      padding: 10px;

      position: relative;
      width: 140px;
      height: 140px;
      transform: rotate(-10deg);

      span {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 15px;
        letter-spacing: 0px;
        text-align: center;

        color: $color-default-white;
      }

      svg {
        width: 25px;
        height: 25px;
      }
    }
    @media (min-width: $desktops) {
      top: -15px;
      left: 10px;
      padding: 10px;

      position: relative;
      width: 140px;
      height: 140px;
      transform: rotate(-10deg);

      span {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 15px;
        letter-spacing: 0px;
        text-align: center;

        color: $color-default-white;
      }

      svg {
        width: 25px;
        height: 25px;
      }
    }

    @media (min-width: $desktops-2) {
      top: -15px;
      left: 10px;
    }

    @media (min-width: $desktops-4) {
      top: -15px;
      left: 35px;

      position: relative;
      width: 190px;
      height: 190px;
      transform: rotate(0);

      span {
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0px;
        text-align: center;
        color: $color-default-white;
      }

      svg {
        width: 30px;
        height: 30px;
      }
    }
  }

  &--roof {
    @media (min-width: $tablets) {
      top: -150px;
      left: 95%;
      padding: 10px;

      position: relative;
      width: 140px;
      height: 140px;
      transform: rotate(-10deg);
      z-index: 1;

      span {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 15px;
        letter-spacing: 0px;
        text-align: center;

        color: $color-default-white;
      }

      svg {
        width: 25px;
        height: 25px;
      }
    }
    @media (min-width: $desktops) {
      top: -140px;
      left: 87%;
      padding: 10px;

      position: relative;
      width: 140px;
      height: 140px;

      svg {
        width: 25px;
        height: 25px;
      }
    }

    @media (min-width: $desktops-2) {
      top: -131px;
      left: 85%;
    }

    @media (min-width: $desktops-4) {
      top: -170px;
      left: 90%;

      position: relative;
      width: 190px;
      height: 190px;

      span {
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0px;
        text-align: center;
      }
    }
  }
}
