.epoque-slide {
  margin: -100px 0 !important;
  padding: 100px 0 !important;
  overflow: hidden;
  transition: 0.3s all;
}

.swiper-slide-active + .epoque-slide {
  @media (min-width: $tablets) {
    left: 0
  }
  @media (min-width: $desktops) {
    left: -10%;
  }

  @media (min-width: $desktops-2) {
    left: -7%;
  }

  @media (min-width: $desktops-4) {
    left: -3%;
  }

  @media (min-width: 1900px) {
    left: -9%;
  }
}

.epoque {
  @media (max-width: $mobiles-max) {
    margin-top: 40px;
    margin-bottom: 70px;
  }
}

.epoque__inner {
  display: flex;

  @media (min-width: $tablets) {
    margin-top: 35px;
  }

  @media (min-width: $desktops) {
    margin-top: 40px;
  }

  @media (min-width: $desktops-2) {
    margin-top: 30px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 40px;
  }
}

.epoque__texts {
  position: relative;

  @media (min-width: $tablets) {
    width: 40.5vw;
    margin-right: 4vw;
  }

  @media (min-width: $desktops) {
    width: 33vw;
    margin-right: 4vw;
  }

  @media (min-width: $desktops-2) {
    width: 26vw;
    margin-right: 3vw;
  }

  @media (min-width: $desktops-4) {
    width: 25vw;
    margin-right: 5vw;
  }

  @media (max-width: $mobiles-max) {
    position: relative;
  }

  h3 {
    margin: 20px 0 30px;
    position: relative;

    @media (min-width: $tablets) {
      margin: 215px 0 15px;
      font-size: 25px;
      line-height: 25px;
      padding-bottom: 15px;
      border-bottom: 1px solid #fff;
      &:before {
        content: url("../img/heading.svg");
        position: absolute;
        left: -26px;
        top: -20px;
        z-index: -1;
      }
    }

    @media (min-width: $desktops) {
      font-size: 25px;
      line-height: 25px;
      margin-top: 100px;
      padding-bottom: 15px;
      margin-bottom: 15px;
      border-bottom: 1px solid #fff;

    }

    @media (min-width: $desktops-4) {
      font-size: 30px;
      line-height: 30px;
      margin-top: 150px;
    }

    @media (max-width: $mobiles-max) {
      margin: 20px 0 30px;
    }
  }

  p {
    text-indent: 32px;
    // font-size: 20px;
    // line-height: 27px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    // margin: 0;
    // margin-bottom: 30px;

    @media (min-width: $tablets) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 19px;
      letter-spacing: 0px;
      text-align: left;

      &:last-of-type {
        max-width: 85%;
      }
    }

    @media (min-width: $desktops-2) {
      font-size: 14px;
      line-height: 19px;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 27px;
    }

    @media (max-width: $mobiles-max) {
      text-indent: 0;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
    }
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    margin-right: 0;
  }
}

.epoque__picture {
  display: none;

  top: 0;
  position: absolute;
  width: auto;
  height: 100vh;
  right: -106vw;

  @media (min-width: $desktops) {
    right: -66vw;
    width: 124vw;
  }

  @media (min-width: $desktops-2) {
    // right: -38vw;
    width: 132vw;
  }

  @media (min-width: $desktops-4) {
    width: 131vw;
  }

  @media (max-width: $mobiles-max) {
    display: block;
    position: relative;
    top: 0;
    right: 0;
    height: 240px;
    width: 100%;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;

    @media (min-width: $desktops) {
      object-fit: contain;
      width: 100%;
      height: auto;
    }
  }
}

.epoque__wrapper {
  position: relative;
}

// .epoque__picture-wrapper {
//   @media (max-width: $mobiles-max) {
//     position: relative;

//     .epoque__preview {
//     }
//   }
// }

.epoque__preview {
  @media (max-width: $mobiles-max) {
    position: absolute;
    top: -290px;
    left: 0;
    width: 100px;
    height: 100px;
  }
}

.epoque__things {
  width: 140px;
  height: 140px;
  content: none;
  position: absolute;
  right: -10vw;
  bottom: 10px;
  z-index: 10;

  @media (min-width: $desktops) {
    content: url("../img/thing.svg");
    right: -10vw;
    bottom: 10px;
  }

  @media (min-width: $desktops-2) {
    right: -8vw;
    bottom: 0;
  }

  @media (min-width: $desktops-4) {
    width: 190px;
    height: 190px;
  }
}

.roof-slide {
  background-image: url("../img/roof.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  min-height: 100vh !important;
  margin: -100px 0;
  padding: 100px 0;
  transition: 0.3s all;
  left: 0;

  &.swiper-slide-prev {
    left: auto;

    @media (min-width: $desktops) {
      right: -17vw;
    }

    @media (min-width: $desktops-2) {
      right: -27vw;
    }

    @media (min-width: $desktops-4) {
      right: -27vw;
    }
  }
}

.swiper-slide-active + .roof-slide {
  @media (min-width: $tablets) {
    left: -53vw;
    background-position: inherit;
  }
  @media (min-width: $desktops) {
    left: -56vw;
    background-position: inherit;
  }

  @media (min-width: $desktops-2) {
    left: -66vw;
  }

  @media (min-width: $desktops-4) {
    left: -65vw;
  }

  @media (min-width: 2400px) {
    left: -55vw;
  }
}
